import React from 'react';
import Fade from 'react-reveal/Fade';
import { Link } from 'gatsby';
import './listboard.css';
import adrenaline from '../../images/adrenaline.jpg';
import akfa from '../../images/akfa.jpg';
import bars from '../../images/bars.jpg';
import bon from '../../images/bon.jpg';
import chekhov from '../../images/chekhov.jpg';
import glamour from '../../images/glamour.jpg';
import hp from '../../images/hp.jpg';
import humo from '../../images/humo.jpg';
import podiumItalia from '../../images/podium.jpg';
import terraPro from '../../images/terra.jpg';
import texnomart from '../../images/texnomart.jpg';
import theBlackQuail from '../../images/black.jpg';
import agrover from '../../images/agrover.jpg';
import barsTires from '../../images/bars-tires.jpg';
import korzinka from '../../images/korzinka.jpg';
import pepsi from '../../images/pepsi.jpg';

const Listboard = ({ ru }) => (
    <div className="listboard column">
        <Fade bottom>
        <div className="card card-column" style={{'gridRowEnd': 'span 21'}}>
            <div className="content">
                <Link to={ru ? '/ru/project' : '/project'} className="card-video"
                 state={{ title: 'Akfa University', videoUrl: 'https://youtube.com/embed/2y6uzBOldDA',
                 description: 'В совместной работе с AKFA University, мы решили обойти стороной привычный образ главного героя, тем самым, позволяя зрителю поставить себя на его место. Для воплощения этой идеи в жизнь, наша команда приложила все усилия на создание мизансцен, которые раскрывают атмосферу событий и визуализацию необычного повествования.' }}>
                <div className="card-video-inner">
                    <img src={akfa} alt="Akfa University" />
                </div>
                <span className="card-link">Akfa University</span>
                <p>В совместной работе с AKFA University, мы решили обойти стороной привычный образ главного героя, тем самым, позволяя зрителю поставить себя на его место</p>
                </Link>
            </div>
        </div>

        <div className="card card-column" style={{'gridRowEnd': 'span 19', 'marginTop': '30px'}}>
            <div className="content">
                <Link to={ru ? '/ru/project' : '/project'} className="card-video"
                 state={{ title: 'Adrenaline Rush Uzbekistan', videoUrl: 'https://www.youtube.com/embed/258IWEORXPI',
                 description: 'В совместном проекте с Adrenaline Rush Uzbekistan наша команда работала над мотивационным видеороликом главным героем которого стал чемпион мира по боксу, Шахрам Гиясов. Долгие часы тренировок, преданность любимому делу, упорство и непоколебимость-вот реальная история становления чемпиона. Сказать, что этот проект был интересным- ничего не сказать. Динамичность работы и слаженность действий помогли воплотить этот огненный проект в жизнь. А как мотивируете себя Вы?' }}>
                <div className="card-video-inner">
                    <img src={adrenaline} alt="Adrenaline Rush Uzbekistan" />
                </div>
                <span className="card-link">Adrenaline Rush Uzbekistan</span>
                <p>Сказать, что этот проект был интересным- ничего не сказать</p>
                </Link>
            </div>
        </div>

        <div className="card card-column" style={{'gridRowEnd': 'span 21'}}>
            <div className="content">
                <Link to={ru ? '/ru/project' : '/project'} className="card-video"
                 state={{ title: 'Glamour Boutique', videoUrl: 'https://youtube.com/embed/4cA-48a7PCU',
                 description: 'Сила моды заключается в возможности постоянного перевоплощения, перерождения и изменения, в способности удивлять, поражать и покорять с первого, со второго и даже с тысячного взгляда. Сила высокой моды кроется в уникальности каждого образа, в смелости его создателя, и в вас – в личности, которая не боится высказываться на языке самой красивой из индустрий. Новая коллекция бутика Glamour представляет самые сильные высказывания, цитаты и образы самых сильных модных домов мира.' }}>
                <div className="card-video-inner">
                    <img src={glamour} alt="Glamour Boutique" />
                </div>
                <span className="card-link">Glamour Boutique Tashkent</span>
                <p>Сила моды заключается в возможности постоянного перевоплощения, перерождения и изменения, в способности удивлять, поражать и покорять с первого, со второго и даже с тысячного взгляда</p>
                </Link>
            </div>
        </div>

        <div className="card card-column" style={{'gridRowEnd': 'span 22'}}>
            <div className="content">
                <Link to={ru ? '/ru/project' : '/project'} className="card-video"
                 state={{ title: 'Bars Tires', videoUrl: 'https://youtube.com/embed/y_3YpDLeV7M',
                 description: 'В данном проекте с Bars Tires перед нами стояла задача создать ролик, который бы показывал собственный, индивидуальный рисунок протектора шин, снятый только вживую, без использования компьютерной 3D анимации. Мы решили использовать макро объектив, который также позволил запечатлеть кадры внутри самого завода.' }}>
                <div className="card-video-inner">
                    <img src={bars} alt="Bars Tires" />
                </div>
                <span className="card-link">Bars Tires</span>
                <p>В данном проекте с Bars Tires перед нами стояла задача создать ролик, который бы показывал собственный, индивидуальный рисунок протектора шин, снятый только вживую, без использования компьютерной 3D анимации</p>
                </Link>
            </div>
        </div>
        
        <div className="card card-column" style={{'gridRowEnd': 'span 22'}}>
            <div className="content">
                <Link to={ru ? '/ru/project' : '/project'} className="card-video"
                 state={{ title: 'Bon! Chain of French Coffee Houses', videoUrl: 'https://youtube.com/embed/Pb6hHX_0XKU',
                 description: '«Жизнь как шоколад-ей наслаждаешься кусочек за кусочком, позволяя каждому долго таять на языке». В этом проекте мы решили показать производство шоколада с ретроспективы, тем самым возвращаясь к его истокам - какао-бобам. Такой вот тягучий и ароматный ролик получился у нас в совместной работе с Bon.' }}>
                <div className="card-video-inner">
                    <img src={bon} alt="Bon!" />
                </div>
                <span className="card-link">Bon!</span>
                <p>«Жизнь как шоколад-ей наслаждаешься кусочек за кусочком, позволяя каждому долго таять на языке»</p>
                </Link>
            </div>
        </div>

        <div className="card card-column" style={{'gridRowEnd': 'span 22'}}>
            <div className="content">
                <Link to={ru ? '/ru/project' : '/project'} className="card-video"
                 state={{ title: 'Chekhov Sport Clubs', videoUrl: 'https://youtube.com/embed/Zyzz0qGrIJg',
                 description: 'Be authentic, be with Chekhov! Юбилейный ролик, приуроченный десятилетию спортивного клуба Чехов! Начиная с одного клуба на одноименной улице, сеть смогла стать крупнейшей и узнаваемой сетью в стране, с чем её и поздравляем!' }}>
                <div className="card-video-inner">
                    <img src={chekhov} alt="Chekhov" />
                </div>
                <span className="card-link">Chekhov</span>
                <p>Be authentic, be with Chekhov! Юбилейный ролик, приуроченный десятилетию спортивного клуба Чехов!</p>
                </Link>
            </div>
        </div>

        <div className="card card-column" style={{'gridRowEnd': 'span 21'}}>
            <div className="content">
                <Link to={ru ? '/ru/project' : '/project'} className="card-video"
                 state={{ title: 'HP', videoUrl: 'https://youtube.com/embed/-z1fOkKGcjo',
                 description: 'В проекте HP Central Asia Distribution перед нами стояла задача показать все детали снимаемых товаров только “живой” съёмкой, то есть без использования компьютерной 3D анимации.' }}>
                <div className="card-video-inner">
                    <img src={hp} alt="HP" />
                </div>
                <span className="card-link">HP</span>
                <p>В проекте HP Central Asia Distribution перед нами стояла задача показать все детали снимаемых товаров только “живой” съёмкой, то есть без использования компьютерной 3D анимации</p>
                </Link>
            </div>
        </div>

        <div className="card card-column" style={{'gridRowEnd': 'span 20'}}>
            <div className="content">
                <Link to={ru ? '/ru/project' : '/project'} className="card-video"
                 state={{ title: 'Humo hockey club', videoUrl: 'https://youtube.com/embed/EEsVcFDerTM',
                 description: 'Хоккей ближе, чем ты думаешь! Работая с Humo Hockey Club первой хоккейной командой в Узбекистане нам удалось создать особенный видеоролик, раскрывающий традиции нашей жаркой страны, которая, казалось бы, далека от этого холодного вида спорта.' }}>
                <div className="card-video-inner">
                    <img src={humo} alt="Humo Arena" />
                </div>
                <span className="card-link">Humo Arena</span>
                <p>Хоккей ближе, чем ты думаешь!</p>
                </Link>
            </div>
        </div>

        <div className="card card-column" style={{'gridRowEnd': 'span 20'}}>
            <div className="content">
                <Link to={ru ? '/ru/project' : '/project'} className="card-video"
                 state={{ title: 'Terra Pro', videoUrl: 'https://youtube.com/embed/rU3vyrjdxw0',
                 description: 'В этом проекте с Terra Pro наша команда решила создать яркий, динамичный молодежный ролик, в котором была представлена коллекция современной мужской одежды весна-лето 2019 в стиле casual. Съемка проходила в довольно интересном месте для представления look book с новой стороны.' }}>
                <div className="card-video-inner">
                    <img src={terraPro} alt="Terra Pro" />
                </div>
                <span className="card-link">Terra Pro</span>
                <p>В этом проекте с Terra Pro наша команда решила создать яркий, динамичный молодежный ролик</p>
                </Link>
            </div>
        </div>

        <div className="card card-column" style={{'gridRowEnd': 'span 19'}}>
            <div className="content">
                <Link to={ru ? '/ru/project' : '/project'} className="card-video"
                 state={{ title: 'Texnomart', videoUrl: 'https://www.youtube.com/embed/B2taaXJJh4c',
                 description: 'На съёмке «эээ», но в итоге «ооо», или кратко о нашей коллаборации с TBWA Tashkent для летней рекламной кампании Texnomart.' }}>
                <div className="card-video-inner">
                    <img src={texnomart} alt="Texnomart" />
                </div>
                <span className="card-link">Texnomart</span>
                <p>На съёмке «эээ», но в итоге «ооо», или кратко о нашей коллаборации с TBWA Tashkent для летней рекламной кампании Texnomart</p>
                </Link>
            </div>
        </div>

        <div className="card card-column" style={{'gridRowEnd': 'span 20'}}>
            <div className="content">
                <Link to={ru ? '/ru/project' : '/project'} className="card-video"
                 state={{ title: 'Podium Italia', videoUrl: 'https://www.youtube.com/embed/JyLZfW6vpUk',
                 description: 'Режиссерская версия нашего совместного проекта с Podium Italia.' }}>
                <div className="card-video-inner">
                    <img src={podiumItalia} alt="Podium Italia" />
                </div>
                <span className="card-link">Podium Italia</span>
                <p>Режиссерская версия нашего совместного проекта с Podium Italia</p>
                </Link>
            </div>
        </div>

        <div className="card card-column" style={{'gridRowEnd': 'span 20'}}>
            <div className="content">
                <Link to={ru ? '/ru/project' : '/project'} className="card-video"
                 state={{ title: 'The Black Quail', videoUrl: 'https://www.youtube.com/embed/2J4bZ9x3SD0',
                 description: 'Данная работа была приурочена не только к созданию имиджа бренда The Black Quail, но и к дню всех влюблённых,14 февраля. Задачей было создать ролик, который бы не только соответствовал миссии бренда, как создающего изделия ручной работы, так и добавить элемент дня влюблённых.' }}>
                <div className="card-video-inner">
                    <img src={theBlackQuail} alt="The Black Quail" />
                </div>
                <span className="card-link">The Black Quail</span>
                <p>Данная работа была приурочена не только к созданию имиджа бренда The Black Quail, но и к дню всех влюблённых,14 февраля</p>
                </Link>
            </div>
        </div>

        <div className="card card-column" style={{'gridRowEnd': 'span 21'}}>
            <div className="content">
                <Link to={ru ? '/ru/project' : '/project'} className="card-video"
                 state={{ title: 'Agrover', videoUrl: 'https://www.youtube.com/embed/aPRqONBLX6Q',
                 description: 'Работа приурочена к Международному Дню Картофеля. Задачей являлось показать довольно знакомый всем продукт с новой стороны, с которой зритель мог бы прочувствовать всю ценность и эмоции, связанные с использованием картофеля в готовке. Так же мы постарались запечатлеть кадры многовекового труда фермеров, вложенного в выращивание и сбор картошки. Ролик представляет несколько поколений, разных целевых групп, которых объединяет общая любовь к картофелю и возможность собраться всей семьёй, передать заботу и любовь, любимые рецепты будущим поколениям.' }}>
                <div className="card-video-inner">
                    <img src={agrover} alt="Agrover" />
                </div>
                <span className="card-link">Agrover</span>
                <p>Работа приурочена к Международному Дню Картофеля</p>
                </Link>
            </div>
        </div>

        <div className="card card-column" style={{'gridRowEnd': 'span 20'}}>
            <div className="content">
                <Link to={ru ? '/ru/project' : '/project'} className="card-video"
                 state={{ title: 'Bars Tires', videoUrl: 'https://www.youtube.com/embed/y_3YpDLeV7M',
                 description: 'Выбор чемпионов - это определенно Bars Tires. Совместно с Русланом Нуритдиновым, олимпийским чемпионом по тяжёлой атлетике, мы создали ролик, показывающий особенности шин Bars и отличительный дизайн.' }}>
                <div className="card-video-inner">
                    <img src={barsTires} alt="Bars Tires" />
                </div>
                <span className="card-link">Bars Tires</span>
                <p>Выбор чемпионов - это определенно Bars Tires</p>
                </Link>
            </div>
        </div>

        <div className="card card-column" style={{'gridRowEnd': 'span 20'}}>
            <div className="content">
                <Link to={ru ? '/ru/project' : '/project'} className="card-video"
                 state={{ title: 'Korzinka', videoUrl: 'https://www.youtube.com/embed/prBIGhNEAeg',
                 description: 'Совместно с TBWA Tashkent мы создали душевный видеоролик, в котором вот уже 11 лет #ДетиПишутКорзинке Детальная проработка реквизита и старания художников помогли прочувствовать смену поколений от зарождения акции и до наших дней!' }}>
                <div className="card-video-inner">
                    <img src={korzinka} alt="Korzinka" />
                </div>
                <span className="card-link">Korzinka</span>
                <p>Совместно с TBWA Tashkent мы создали душевный видеоролик</p>
                </Link>
            </div>
        </div>

        <div className="card card-column" style={{'gridRowEnd': 'span 19'}}>
            <div className="content">
                <Link to={ru ? '/ru/project' : '/project'} className="card-video"
                 state={{ title: 'Pepsi Uzbekistan', videoUrl: 'https://www.youtube.com/embed/YNsgVhdVSd4',
                 description: 'Мы постарались наполнить видеоролик для Pepsi Uzbekistan ситуациями, знакомыми для каждого. 16 часов съемок и холод за окном не помешали съемочной команде и актерам перенестись в атмосферу праздника!' }}>
                <div className="card-video-inner">
                    <img src={pepsi} alt="Pepsi Uzbekistan" />
                </div>
                <span className="card-link">Pepsi Uzbekistan</span>
                <p>Мы постарались наполнить видеоролик для Pepsi Uzbekistan ситуациями, знакомыми для каждого</p>
                </Link>
            </div>
        </div>
    </Fade>
    </div>
)

export default Listboard;